import React, {useEffect, useState} from 'react';
import axios from "axios";
import Card from "./Card";
import SelectRegion from "./SelectRegion";

const FranceData = () => {
    //State
    const [data, setData] = useState([]);
    const [playOnce, setPlayOnce] = useState(true);


    //Effect
    useEffect(() =>{
        if (playOnce){
            axios.get("https://coronavirusapi-france.now.sh/FranceLiveGlobalData").then((res) =>{
                setData(res.data.FranceGlobalLiveData);
                setPlayOnce(false)
            });
        }
    }, [data, playOnce]);

    //Render
    return (
        <>
            {data.map((deps) => (
                <div key={deps.code}>
                    <h1>Chiffres du covid pour la {deps.nom}</h1>
                    <p className="desc">Vous trouverez ci-dessous tous les chiffres clé lié à la pendémie sur la france entière mais également par région</p>
                    <div className="data-content">
                        <Card deps={deps} />
                        <SelectRegion/>
                    </div>
                </div>
            ))}
        </>
    );
};

export default FranceData;
