import React, { Component } from 'react'
import Select from 'react-select'
import axios from 'axios'

export default class App extends Component {

    constructor(props){
        super(props)
        this.state = {
            selectOptions : [],
            code: "",
            nom: '',
            show: false
        }
    }

    async getOptions(){
        const res = await axios.get('https://coronavirusapi-france.now.sh/AllLiveData')
        const data = res.data.allLiveFranceData

        const options = data.map(d => ({
            "value" : d.code,
            "label" : d.nom,
            "hospitalises" : d.hospitalises,
            "reanimation" : d.reanimation,
            "nouvellesHospitalisations" : d.nouvellesHospitalisations,
            "nouvellesReanimations" : d.nouvellesReanimations,
            "gueris" : d.gueris,
            "deces" : d.deces

        }))
        this.setState({selectOptions: options})

    }

    handleChange(e){
        this.setState({
            code:e.value,
            nom:e.label,
            hospitalises:e.hospitalises,
            reanimation:e.reanimation,
            nouvellesHospitalisations:e.nouvellesHospitalisations,
            nouvellesReanimations:e.nouvellesReanimations,
            gueris:e.gueris,
            deces:e.deces,
            show:true
        })
    }

    componentDidMount(){
        this.getOptions()
    }

    render() {
        const {show} = this.state;
        return (
            <div className="region">
                <h2>Par région</h2>
                <Select options={this.state.selectOptions}
                        placeholder="choisir une région"
                        onChange={this.handleChange.bind(this)}
                        onClick={()=> this.setState({show: true })}/>

                {show &&
                <div className="data-container" >
                    <h3>{this.state.nom}</h3>
                    <ul>
                        <li>Personnes hospitalisés <strong>{this.state.hospitalises}</strong></li>
                        <li>Dont <strong>{this.state.reanimation}</strong> en réanimation.</li>
                        <li>depuis hier <strong>{this.state.nouvellesHospitalisations}</strong> Nouvelles Hospitalisations </li>
                        <li>Et <strong>{this.state.nouvellesReanimations}</strong> en Reanimation.</li>
                        <li>Nombres de personnes  gueris <strong>{this.state.gueris}</strong></li>
                        <li>Pour <strong>{this.state.deces}</strong> deces depuis le début de la pandémie</li>
                    </ul>
                    <button onClick={()=> this.setState({show: false })}>Supprimer la recherche</button>
                </div>
                }
            </div>
        )
    }
}
